.get-in-touch{
	background: white;
	max-width: 850px;
    margin: 0 auto;
    position: relative;
    top: 50%;
    -webkit-transform: translateY(25%);
            transform: translateY(25%);
}

.get-in-touch .title {
	text-align: center;
	font-family: Raleway, sans-serif;
	text-transform: uppercase;
	letter-spacing: 3px;
	font-size: 36px;
	line-height: 48px;
	padding-bottom: 48px;
}
.contact-form .form-field {
	position: relative;
	margin: 32px 0;
}
.contact-form .input-text {
	display: block;
	width: 100%;
	height: 36px;
	border-width: 0 0 2px 0;
	border-color: #000;
	font-family: Lusitana, serif;
	font-size: 18px;
	line-height: 26px;
	font-weight: 400;
}
.contact-form .input-text:focus {
	outline: none;
}
.contact-form .input-text:focus + .label, .contact-form .input-text.not-empty + .label {
	-webkit-transform: translateY(-24px);
	        transform: translateY(-24px);
}
.contact-form .label {
	position: absolute;
	left: 20px;
	bottom: 11px;
	font-family: Lusitana, serif;
	font-size: 18px;
	line-height: 26px;
	font-weight: 400;
	color: #888;
	cursor: text;
	-webkit-transition: -webkit-transform 0.2s ease-in-out;
	transition: -webkit-transform 0.2s ease-in-out;
	transition: transform 0.2s ease-in-out;
	transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}
.contact-form .submit-btn {
	display: inline-block;
	background-color: #000;
	color: #fff;
	font-family: Raleway, sans-serif;
	text-transform: uppercase;
	letter-spacing: 2px;
	font-size: 16px;
	line-height: 24px;
	padding: 8px 16px;
	border: none;
	cursor: pointer;
}

.contact-row {
	margin: -20px 0;
}
.contact-row:after {
	content: "";
	display: table;
	clear: both;
}
.contact-row .col {
	padding: 0 20px;
	float: left;
	box-sizing: border-box;
}
.contact-row .col.x-50 {
	width: 50%;
}
.contact-row .col.x-100 {
	width: 100%;
}
.content-wrapper {
	min-height: 100%;
	position: relative;
}

.hide-this{
	display: none;
}


