  /*//////////////////////////////////////////////////////////////////
  [ RESTYLE TAG ]*/
  
  * {
	  margin: 0px; 
	  padding: 0px; 
	  box-sizing: border-box;
  }
  
  body, html {
	  height: 100%;
	  font-family: "Poppins", sans-serif;
  }
  
  /*---------------------------------------------*/
  a {
	  font-family: "Poppins";
	  font-size: 14px;
	  line-height: 1.7;
	  color: #666666;
	  margin: 0px;
	  transition: all 0.4s;
	  -webkit-transition: all 0.4s;
	-o-transition: all 0.4s;
	-moz-transition: all 0.4s;
  }
  
  a:focus {
	  outline: none !important;
  }
  
  a:hover {
	  text-decoration: none;
  }
  
  /*---------------------------------------------*/
  h1,h2,h3,h4,h5,h6 {
	  margin: 0px;
  }
  
  p {
	  font-family: "Poppins";
	  font-size: 14px;
	  line-height: 1.7;
	  color: #666666;
	  margin: 0px;
  }
  
  ul, li {
	  margin: 0px;
	  list-style-type: none;
  }
  
  
  /*---------------------------------------------*/
  input {
	  outline: none;
	  border: none;
  }
  
  input[type="number"] {
	  -moz-appearance: textfield;
	  appearance: none;
	  -webkit-appearance: none;
  }
  
  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
	  -webkit-appearance: none;
  }
  
  textarea {
	outline: none;
	border: none;
  }
  
  textarea:focus, input:focus {
	border-color: transparent !important;
  }
  
  input:focus::-webkit-input-placeholder { color:transparent; }
  input:focus:-moz-placeholder { color:transparent; }
  input:focus::-moz-placeholder { color:transparent; }
  input:focus:-ms-input-placeholder { color:transparent; }
  
  textarea:focus::-webkit-input-placeholder { color:transparent; }
  textarea:focus:-moz-placeholder { color:transparent; }
  textarea:focus::-moz-placeholder { color:transparent; }
  textarea:focus:-ms-input-placeholder { color:transparent; }
  
  input::-webkit-input-placeholder {color: #999999;}
  input:-moz-placeholder {color: #999999;}
  input::-moz-placeholder {color: #999999;}
  input:-ms-input-placeholder {color: #999999;}
  
  textarea::-webkit-input-placeholder {color: #999999;}
  textarea:-moz-placeholder {color: #999999;}
  textarea::-moz-placeholder {color: #999999;}
  textarea:-ms-input-placeholder {color: #999999;}
  
  /*---------------------------------------------*/
  button {
	  outline: none !important;
	  border: none;
	  background: transparent;
  }
  
  button:hover {
	  cursor: pointer;
  }
  
  iframe {
	  border: none !important;
  }
  
  
  
  
  /*//////////////////////////////////////////////////////////////////
  [ Contact ]*/
  
  .container-contact2-form {
	width: 100%;  
	min-height: 100vh;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	padding: 15px;
	background: transparent;
	position: relative;
	z-index: 1;
}
  
.contact2-form-map {
	position: absolute;
	z-index: -2;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}
  
.wrap-contact2-form {
	width: 80%;
	max-width: 670px;
	background: #fff;
	border-radius: 10px;
	overflow: hidden;
	position: relative;
	margin: auto;
    top: 5em;
}


.contact2-form-form-title {
	width: 100%;
	position: relative;
	z-index: 1;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	align-items: center;
	background-image: url(/static/media/bg-01.57017de0.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
  
	padding: 5em 1em;
  }
  
  .contact2-form-form-title-1 {
	font-family: "Poppins";
	font-weight: bold;
	font-size: 20px;
	color: #fff;
	line-height: 1.2;
	text-align: center;
	padding-bottom: 7px;
  }
  
  .contact2-form-form-title-2 {
	font-family: "Poppins";
	font-size: 15px;
	color: #fff;
	line-height: 1.5;
	text-align: center;
  }
  
  
  .contact2-form-form-title::before {
	content: "";
	display: block;
	position: absolute;
	z-index: -1;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background-color: rgba(54,84,99,0.7);
  }
  
  
  /*==================================================================
  [ Form ]*/
  
  .contact2-form-form {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding: 4em 8em;
  }
  
  
  /*------------------------------------------------------------------
  [ Input ]*/
  
  .wrap-contact2-input {
	width: 100%;
	position: relative;
	/* border-bottom: 1px solid #b2b2b2; */
	margin-bottom: 26px;
  }
  
  .label-contact2-input {
	font-family: "Poppins";
	font-size: 15px;
	color: #808080;
	line-height: 1.2;
	text-align: right;
  
	position: absolute;
	top: 14px;
	left: -105px;
	width: 80px;
  
  }
  
  /*---------------------------------------------*/
  .contact2-input {
	font-family: "Poppins";
	font-size: 15px;
	color: #555555;
	line-height: 1.2;
  
	display: block;
	width: 100%;
	background: transparent;
	padding: 0 5px;
  }
  
  .focus-contact2-input {
	position: absolute;
	display: block;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	pointer-events: none;
  }
  
  .focus-contact2-input::before {
	content: "";
	display: block;
	position: absolute;
	bottom: -1px;
	left: 0;
	width: 0;
	height: 1px;
  
	-webkit-transition: all 0.6s;
	transition: all 0.6s;
  
	background: #57b846;
  }
  
  
  /*---------------------------------------------*/
  input.contact2-input {
	height: 45px;
  }
  
  
  textarea.contact2-input {
	min-height: 115px;
	padding-top: 14px;
	padding-bottom: 13px;
  }
  
  
  .contact2-input:focus + .focus-contact2-input::before {
	width: 100%;
  }
  
  .has-val.contact2-input + .focus-contact2-input::before {
	width: 100%;
  }
  
  
  /*------------------------------------------------------------------
  [ Button ]*/
  .container-contact2-form-form-btn {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	padding-top: 8px;
    line-height: 32px;
  }
  
  .contact2-form-form-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0 20px;
	min-width: 160px;
	height: 50px;
	background-color: #57b846;
	border-radius: 25px;
  
	font-family: "Poppins";
	font-size: 16px;
	color: #fff;
	line-height: 1.2;
  
	-webkit-transition: all 0.4s;
	transition: all 0.4s;
  }
  
  .contact2-form-form-btn i {
	-webkit-transition: all 0.4s;
	transition: all 0.4s;
  }
  
  .contact2-form-form-btn:hover {
	background-color: #333333;
  }
  
  .contact2-form-form-btn:hover i {
	-webkit-transform: translateX(10px);
	transform: translateX(10px);
  }
  
  
  /*------------------------------------------------------------------
  [ Responsive ]*/
  
  @media (max-width: 576px) {
	.contact2-form-form {
	  padding: 43px 15px 57px 117px;
	}
  }
  
  @media (max-width: 480px) {
	.contact2-form-form {
	  padding: 43px 15px 57px 15px;
	}
  
	.label-contact2-input {
	  text-align: left;
	  position: unset;
	  top: unset;
	  left: unset;
	  width: 100%;
	  padding: 0 5px;
	}
  }
  
  
  /*------------------------------------------------------------------
  [ Alert validate ]*/
  
  .validate-input {
	position: relative;
  }
  
  .alert-validate::before {
	content: attr(data-validate);
	position: absolute;
	max-width: 70%;
	background-color: #fff;
	border: 1px solid #c80000;
	border-radius: 2px;
	padding: 4px 25px 4px 10px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	right: 2px;
	pointer-events: none;
  
	font-family: "Poppins";
	color: #c80000;
	font-size: 13px;
	line-height: 1.4;
	text-align: left;
  
	visibility: hidden;
	opacity: 0;
  
	-webkit-transition: opacity 0.4s;
	transition: opacity 0.4s;
  }
  
  .alert-validate::after {
	content: "\F06A";
	font-family: FontAwesome;
	display: block;
	position: absolute;
	color: #c80000;
	font-size: 15px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	right: 8px;
  }
  
  .alert-validate:hover:before {
	visibility: visible;
	opacity: 1;
  }
  
  @media (max-width: 992px) {
		.alert-validate::before {
			visibility: visible;
			opacity: 1;
		}
  }
  
  
  
  

  
